nav {
   position:absolute;
    top: 0;
    width: 100%;
    height: 100px;
    background: unset;
    padding-bottom: 30px;
  
    }


.list {
    list-style-type: none;
    background: unset;
    text-decoration: none !important;
   height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    position: relative;
}
li a{
    color: black !important;
    text-decoration: none !important;
  }
.items {
    margin-right: 40px;
    font-size: 20px;
    text-transform: uppercase;
    color: black;
    cursor: pointer;
}

.btn {
    display: none;
    position: absolute;
    right: 10px;
    top: 6px;
    padding: 5px;
    color: #424242;
    font-size: 18px;
    background-color: white;
    border: 2px solid black;
    border-radius: 5px;
    width: 2.5rem;
    height: 2.8rem;
    margin-bottom: 5px;
}

 

    @media screen and (max-width: 500px){
        .slide-left {
            -webkit-animation: slide-left 0.7s both;
                    animation: slide-left 0.7s both;
                   z-index: 2 !important;
                   width: 100%;
                   height: fit-content;
                    
        }
 
        .btn {
            display: block; 
        }
        .list {
            flex-direction: column;
            height: auto;
            width: 100%;
        }
        .items:nth-child(1){
            border-top: 1px solid rgba(255, 255, 255, 0.555);
            margin-top: 50px;
       
        }
        .items {
            width: 100%;
            border-top: 1px solid rgba(255, 255, 255, 0.555);
            text-align: center;
            margin-right: 0px;
            padding: 20px 0;
            background-color: white;
           
        
        }
        .navbar-brand{
           
        }

    }
    .logo-img{
    width: 45px;
    height: 45px;
    border-radius: 50%;
    overflow: hidden;
    margin-top: -50px;
    }
    .navbar-brand{
        position: absolute;
        left: 1em;
        top: 1em;
        opacity: 70%;
    }
    li img{
        float: left;
    }
.activeLink{
    text-decoration:overline;
    text-decoration-color: black;
}
.active  {
    border-bottom: 3px solid rgb(95,0,0);
  }
  @media screen and (min-width: 550px){
    .nav__link {
        background-image: linear-gradient(
          to bottom,
          rgb(95,0,0),
          rgb(106, 99, 99)50%,
          #000 50%
        );
        background-size: 200% 100%;
        background-position: -100%;
        display: inline-block;
        padding: 5px 0;
        position: relative;
        -webkit-background-clip: text;
        -webkit-text-fill-color: transparent;
        transition: all 0.3s ease-in-out;
      }
      
      .nav__link:before {
        content: '';
        background: rgb(95,0,0);
        display: block;
        position: absolute;
        bottom: -3px;
        left: 0;
        width: 0;
        height: 3px;
        transition: all 0.3s ease-in-out;
      }
      
      .nav__link:hover {
       background-position: 0;
      }
      
      .nav__link:hover::before {
        width:100%;
      }
  }
  @media screen and (max-width: 550px){
    .items:hover a{
        background-image: linear-gradient(
            to bottom,
            rgb(95,0,0),
            rgb(106, 99, 99)50%,
            #000 50%
          );
          background-size: 200% 100%;
          background-position: -100%;
          display: inline-block;
          position: relative;
          -webkit-background-clip: text;
          -webkit-text-fill-color: transparent;
          transition: all 0.3s ease-in-out;
    }
  }
    